//This script creates a phrase with a random adjective and a clicable button to reload the phrase.

let canvasDiv = document.getElementById('canvasContainer');
let divWidth = canvasDiv.offsetWidth;
let divHeight = canvasDiv.offsetHeight;

let table, box, list, myFont, img;
let textX, textY;
let text1,text2;
let margin = 5;
let fontSize;
let spaceBetweenLines;

function preload() {
    table = loadTable('data/adjectives.csv', 'csv', 'header');
    myFont = loadFont('font/PIXY.otf')
    img = loadImage('img/button.svg');
}

function setup() {
    createCanvas(divWidth, divHeight).parent("canvasContainer");
    background('#FDE033');
    list = table.getColumn('adjective');

    //get random items from the array
    text1 = "YOU ARE " + random(list).toUpperCase();
    text2 = "HERE IS YOUR PROMOCODE."
}


function draw() {
    background('#FDE033');
    fill(0)
    textAlign(LEFT);
    fontSize = divWidth / 17;  
    spaceBetweenLines =  divWidth/72;
    textSize(fontSize);
    textFont(myFont);    
    

    //get sizes of textboxes
    box1 = myFont.textBounds(text1, 0, 0, fontSize);
    box2 = myFont.textBounds(text2, 0, 0, fontSize);

    buttonH = box1.h + 5;


    textX = (divWidth - box2.w) / 2; 
    textY = 140;
    
    //rectangles
    rect(textX - margin*2, textY - margin, box1.w + buttonH*2 + margin, box1.h + 10)
    rect(textX - margin*2, textY + box1.h + spaceBetweenLines - margin, box2.w + 16, box2.h + 10)

    //text    
    fill('#FDE033')
    text(text1, textX, textY + box1.h);
    text(text2, textX, textY + (box1.h*2) + spaceBetweenLines);

    //button
    fill(255);
    buttonX = textX + box1.w + buttonH;
    buttonY = textY + box1.h / 2;
    
    imageMode(CENTER);
    image(img, buttonX, buttonY, buttonH, buttonH)
}


function mousePressed() {
    if (dist(mouseX, mouseY, buttonX, buttonY) < buttonH) {
        text1 = "YOU ARE " + random(list).toUpperCase();
    }
}

function windowResized() {
    divWidth = canvasDiv.offsetWidth;
    divHeight = canvasDiv.offsetHeight;
    resizeCanvas(divWidth, divHeight);
}